body {
  font-family: 'Ubuntu', sans-serif;
  box-sizing: border-box; }

h3 {
  font-family: Ubuntu;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px; }

h4 {
  font-family: Ubuntu;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.container-intro {
  background-color: #FFFFFF;
  margin: 80px 100px 0 100px;
  position: relative; }
  .container-intro__text {
    font-family: 'Ubuntu', sans-serif;
    font-style: italic;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px; }
  .container-intro__text:before {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background: #F19932;
    border-radius: 2px;
    margin: 5px 0 0 -20px; }

.container-warn {
  position: relative;
  left: 0;
  width: 473px;
  height: 90px;
  background: rgba(251, 217, 44, 0.16);
  border-radius: 0px 4px 4px 0px; }
  .container-warn__title {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 15px;
    color: #333333;
    padding: 20px 0 0 100px; }
  .container-warn__text {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 11px;
    color: #333333;
    width: 314px;
    padding-left: 100px; }
  .container-warn__icon {
    background-image: url(src/img/vector.svg);
    width: 19.06px;
    height: 17.01px;
    position: absolute;
    right: 0;
    top: 0;
    margin: 36px 20px 0 0; }

.container-forms {
  margin-left: 100px;
  position: relative;
  margin-top: 20px; }
  .container-forms__field {
    width: 400px;
    height: 50px;
    margin-right: 20px;
    border: none;
    background-color: #F8F7FA;
    border-radius: 2px; }
  .container-forms__field:hover {
    background: #F1F5F9; }
  .container-forms__field:focus-within {
    width: 396px;
    height: 46px;
    background: #FFFFFF;
    border: 2px solid rgba(47, 128, 237, 0.75);
    border-radius: 2px;
    transition: 0.2s; }
  .container-forms__field:last-child {
    margin-right: 0; }
  .container-forms__input {
    border: none;
    background-color: transparent;
    outline: none; }
  .container-forms__col {
    display: flex;
    margin-right: 100px;
    margin-bottom: 37px; }
    .container-forms__col.container-forms__col_first input {
      border-bottom: 1px solid rgba(0, 0, 0, 0.05); }
    .container-forms__col.container-forms__col_first ::placeholder {
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      color: #000000; }
    .container-forms__col.container-forms__col_second ::placeholder {
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      color: rgba(0, 0, 0, 0.5); }

.container-forms__label_grey {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  color: rgba(0, 0, 0, 0.5); }

.container-forms__label_red {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  color: #F64C4C; }

.container-forms__label_blue {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  color: #2F80ED; }

.container-forms__label_black {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  color: #000000; }

.container-forms__input {
  width: 100%;
  margin-top: 4px; }

.container-description {
  margin: 0 100px; }
  .container-description__about {
    width: 1240px;
    height: 122px;
    background: #F8F7FA;
    border-radius: 2px; }
    .container-description__about h4 {
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 11px;
      line-height: 13px;
      color: rgba(0, 0, 0, 0.5);
      padding: 8px 12px 0 12px;
      margin: 0; }
    .container-description__about p {
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px;
      color: #000000;
      margin: 0 12px 8px 12px;
      text-decoration: underline;
      text-decoration-color: rgba(0, 0, 0, 0.05); }
  .container-description__checkbox {
    margin: 20px 0; }
    .container-description__checkbox input {
      display: none; }
    .container-description__checkbox label {
      padding-left: 18px; }
    .container-description__checkbox label span:hover {
      background: rgba(47, 128, 237, 0.05); }
    .container-description__checkbox label::before {
      content: '';
      display: inline-block;
      width: 16px;
      height: 16px;
      vertical-align: middle;
      border: 1px solid #3C8AFB;
      border-radius: 5px;
      margin-right: 8px;
      transition: 0.2s; }
    .container-description__checkbox input:checked + label::before {
      content: '';
      background-size: 100%;
      background-image: url(src/img/icon.svg); }
    .container-description__checkbox input:hover + label::before {
      content: '';
      background-size: 100%;
      background-image: url(src/img/icon.svg); }
  .container-description__lab {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #182C40; }
  .container-description__field {
    border: none;
    width: 100%;
    height: 122px;
    background: #F8F7FA;
    border-radius: 2px;
    margin: 0;
    padding: 0; }
    .container-description__field label {
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 11px;
      line-height: 13px;
      color: #000000;
      padding: 8px 12px 0 12px; }
    .container-description__field textarea {
      width: 95%;
      background: #F8F7FA;
      border: none;
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 14px;
      line-height: 18px;
      color: rgba(0, 0, 0, 0.5);
      padding: 0 12px 8px 12px;
      outline: none; }

.container-buttons {
  margin: 32px 100px 145px 100px;
  display: flex;
  justify-content: space-between; }
  .container-buttons__send {
    width: 216px;
    height: 45px;
    background: #2F80ED;
    border-radius: 2px;
    border: none;
    text-decoration: none;
    cursor: pointer;
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    margin-right: 20px; }
  .container-buttons__send:hover {
    background: #2974D9; }
  .container-buttons__send:focus {
    border: 2px solid rgba(47, 128, 237, 0.5); }
  .container-buttons__send:active {
    background: #236ACB; }
  .container-buttons__send:disabled {
    background: #236ACB;
    color: #FFFFFF; }
  .container-buttons__cancel {
    width: 92px;
    height: 45px;
    background: #E9E9E9;
    border-radius: 2px;
    border: none;
    text-decoration: none;
    cursor: pointer;
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #333333; }
  .container-buttons__safe {
    width: 231px;
    height: 45px;
    background: #E9E9E9;
    border-radius: 2px;
    border: none;
    text-decoration: none;
    cursor: pointer;
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #333333; }
  .container-buttons__safe:hover {
    background: #DBDBDB; }
  .container-buttons__safe:focus {
    border: 2px solid #DBDBDB; }
  .container-buttons__safe:active {
    background: #C4C4C4; }
  .container-buttons__safe:disabled {
    background: #C4C4C4;
    color: rgba(51, 51, 51, 0.5); }
